<template>
    <div>
        <van-popup
            @click-overlay="clickOverlay"
            v-model="showPayType"
            :position="position"
            :style="{ height: '50%' }"
        >
        <div>
            <div class="selectpay">
                <ul>
                    <li @click="chosePayType(item,index)" v-for="(item, index) in pay" :key="index" class="flex flex--justify-content--space-between flex--align-items--center">
                        <div 
                            v-if="!(item.type == 3 && (needPayType != 3 || needPayType != 6 || needPayType != 7))"
                            class="li flex flex--justify-content--space-between flex--align-items--center">
                            <div class="flex flex--align-items--center" >
                                <img :src="item.icon" alt="">
                                <span>{{item.name}}</span>
                            </div>
                            <div class="gold-num" style="color:#999;font-size: 12px;" v-if="item.type == 3">
                                {{goldNum}}
                            </div>
                            <div class="chose" :class="[selectId == index ? 'active' : '']"></div>
                        </div>
                    </li>
                </ul>

                <div class="submit" @click="payOrder"></div>
            </div>
        </div>
        </van-popup>
    </div>
</template>
<script>
import alipay from '@/assets/icon/icon-alipay.png';
import wechat from '@/assets/icon/icon-wechat.png';
import icongold from '@/assets/icon/icon-gold.png';
import api from '@/api/index.js';

export default {
    data(){
        return {
            showPayType: false,
            pay:[
                {
                    name: '',
                    icon: alipay,
                    type: 1
                },
                {
                    name: '',
                    icon: wechat,
                    type: 2
                },
                {
                    name: '',
                    icon: icongold,
                    type: 3
                },
            ],
            pay_type: 1,
            selectId: '',
            payUrl1: this.setApi + '/rest/2.0/competition/competition/pay?pay_type=',           // 1 
            payUrl2: this.setApi + '/rest/2.0/examination/examination/pay?pay_type=',           // 2 
            payUrl3: this.setApi + '/rest/2.0/user/user/plus_pay?pay_type=',                    // 3 
            payUrl4: this.setApi + '/rest/2.0/gold/gold/pay?pay_type=',                         // 4 
            payUrl5: this.setApi + '/rest/2.0/coach/coach/pay?type=',                           // 5 
            payUrl6: this.setApi + '/rest/2.0/reward/reward/reward_pay?pay_type=',              // 6 
            payUrl7: this.setApi + '/rest/2.0/video/video/buy_pay?pay_type=',                       // 7 
            payUrl: '',
            pays: {},
            goldNum: 0
        }
    },
    created(){
        let token = window.localStorage.getItem('token');
        if(token){
            // 
            this.getGoldNum();
        }
    },
    props: {
        position: {
            type: String,
            default: 'bottom'
        },
        needPayType: {
            type: Number,
            default: 1  // 1    2   3   4   5 
        },
        orderdetail: {
            type: Object,
            default: null
        }   
    },
    mounted(){
        // console.log('')
        setTimeout(() => {
            this.onplusReady();
        }, 2000);
        document.addEventListener('plusready', this.plusReady, false);
    },
    destroyed(){
        // document.removeEventListener('plusready', this.plusReady)
    },
    methods: {
        // 
       async getGoldNum(){
            const {data:ret} = await this.$http.post(this.setApi + api.gold,this.querystring.stringify({
                access_token:window.localStorage.getItem('token')
            }))

            this.goldNum = ret.response_data.sum_gold;
            
            if(!Number(ret.response_data.sum_gold)){
                this.pay = this.pay.slice(0, 2);
            }
       },

        /**
         * 
         */
        open(){
            this.showPayType = true;
        },

        /**
         * 
         */
        clickOverlay(){
            this.showPayType = false;
            this.$emit('closeModle');
        },

        /**
         * 
         */
        chosePayType(obj, index){
            if(this.needPayType == 7 && obj.type == 3){
                this.$toast('')
                return
            }

            if(this.needPayType == 3 && obj.type == 3){
                this.$toast('')
                return
            }

            if(this.needPayType == 6 && obj.type == 3){
                this.$toast('')
                return
            }

            this.pay_type = obj.type;
            this.selectId = index;
        },

        /**
         * 
         */
        payOrder(){
            // console.log('====123456=====', this.orderdetail);
            // return
            let token = window.localStorage.getItem('token');
            switch(this.needPayType){
                case 1:
                    // 1 
                    this.payUrl = `${this.payUrl1}${this.pay_type}&access_token=${token}&order_sn=${this.orderdetail.order_sn}`;
                break;
                case 2:
                    // 2 
                    this.payUrl = `${this.payUrl2}${this.pay_type}&access_token=${token}&order_sn=${this.orderdetail.order_sn}`;               
                break;
                case 3:
                    // 3 
                    this.payUrl = `${this.payUrl3}${this.pay_type}&access_token=${token}&order_sn=${this.orderdetail.order_sn}`;
                break;
                case 4:
                    // 4 
                    this.payUrl = `${this.payUrl4}${this.pay_type}&access_token=${token}&order_sn=${this.orderdetail.order_sn}`;
                break;
                case 5:
                    // 5 
                    this.payUrl = `${this.payUrl5}${this.pay_type}&access_token=${token}&order_sn=${this.orderdetail.order_sn}`;
                break;
                case 6:
                    // 6 
                    this.payUrl = `${this.payUrl6}${this.pay_type}&access_token=${token}&order_sn=${this.orderdetail.order_sn}`;
                break;
                case 7:
                    // 7 
                    this.payUrl = `${this.payUrl7}${this.pay_type}&access_token=${token}&order_sn=${this.orderdetail.order_sn}`;
                break;
            }

            this.paaay();
        },

        /**
         * 
         */
        paaay(){
            var id = ''
            // plus.nativeUI.alert("this.pay_type", null, this.pay_type);
            if(this.pay_type == 1){
                // 
                id = 'alipay';
                this.apppay(id);
            }else if(this.pay_type == 2){
                // 
                id = 'wxpay';
                this.apppay(id);
            }else{
                // 
                this.balancePay();
            }



        },

        /**
         * 
         */
        async balancePay(){
            // switch(this.needPayType){
            //     case 1:

            //     break;
            //     case 2:

            //     break;
            //     case 3:

            //     break;
            //     case 4:

            //     break;
            //     case 5:

            //     break;
            // }
            // console.log('=======', this.payUrl);
            const {data:ret} = await this.$http.get(this.payUrl);
            // this.log('', ret);
            if(!ret.error_code){
                if(this.needPayType == 1){
                    this.$toast('');
                }else{
                    this.$toast('');
                }
                this.showPayType = false;
            }
        },

        /**
         * 
         */
        apppay(id){
            let that = this;
            // plus.nativeUI.alert("path", null, this.payUrl);

            // console.log('path--------------' + this.payUrl);

            let xhr = new XMLHttpRequest();
                xhr.onreadystatechange=()=>{
                    // plus.nativeUI.alert(JSON.stringify(xhr.readyState), null, '');
                switch(xhr.readyState){
                    case 4:  
                    if(xhr.status == 200){
                        // plus.nativeUI.alert(JSON.stringify(that.pays[id]),null,'1');
                        // plus.nativeUI.alert(JSON.stringify(xhr.responseText),null,'5');
                        // console.log('=='+JSON.stringify(xhr.responseText));
                        plus.payment.request(that.pays[id], xhr.responseText, (result)=>{
                            // plus.nativeUI.alert(JSON.stringify(xhr.responseText),null,'321');
                            that.showPayType = false;
                            if(this.needPayType == 1){
                                this.$toast('');
                            }else{
                                this.$toast('');
                                
                            }

                            // 
                            if(this.needPayType == 7 || this.needPayType == 3){
                                this.$emit('paySuccess');
                            }else{
                                that.$router.go(-1);
                            }
                            // plus.nativeUI.alert("",()=>{
                                // that.$router.go(-2);
                            // });  
                        },(error)=>{
                            // plus.nativeUI.alert(JSON.stringify(xhr.responseText),null,'123');
                            // plus.nativeUI.alert("" + error.code); 
                            // plus.nativeUI.alert(""); 
                        });  
                    }else{
                        plus.nativeUI.alert(""); 
                    }
                    break;
                    default:
                    break;
                }  
            };
            xhr.open('GET', that.payUrl);  
            xhr.send();  

        },
        plusReady(){
            this.onplusReady();
        },
        onplusReady() {
            let that = this;
            // 
            plus.payment.getChannels(
                function(channels) {
                var content = document.getElementById("dcontent");

                that.payList = channels;
                for (var i in channels) {
                    var channel = channels[i];
                    if (channel.id == "qhpay" || channel.id == "qihoo") {
                        // 360
                        continue;
                    }
                    that.pays[channel.id] = channel;
                    that.checkServices(channel);
                }
                },
                function(e) {
                // outLine('' + e.message);
                }
            );
        },
        // 
        checkServices(pc){
            if(!pc.serviceReady){
                var txt = null;
                switch(pc.id){
                    case 'alipay':
                    txt='';
                    break;
                    default:
                    txt=''+pc.description+'';
                    break;
                }
                plus.nativeUI.confirm(txt, function(e){
                    if(e.index == 0){
                        pc.installService();
                    }
                }, pc.description);
            }
        }
    }
}
</script>
<style lang='less' scoped>
    .selectpay{
        padding: 2.75rem 0  0 .875rem;
        ul{
            li{
                .li {
                    height: 3.25rem;
                    padding-right: .875rem;
                    border-bottom: .0625rem solid #eee;
                    height: 3.25rem;
                    width: 100%;
                }
                div:first-child{
                    color: #333;
                    img{
                        width: 1.8125rem;
                        height: 1.8125rem;
                        margin-right: .8125rem;
                    }
                    span{
                        font-size: .8125rem;
                        color: #333333;
                    }
                }
                div.chose{
                    width: .75rem;
                    height: .75rem;
                    border: .0625rem solid #eee;
                    border-radius: 50%;
                    position: relative;
                    flex-shrink: 0;
                }
                .active::after{
                    content: "";
                    display: block;
                    background: #0EAF0A;
                    width: .5625rem;
                    height: .5625rem;
                    border-radius: 50%;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%,-50%);
                }
            }
        }
        .submit{
            width: 21.6875rem;
            height: 2.9375rem;
            color: #fff;
            text-align: center;
            line-height: 2.9375rem;
            background: #0EAF0A;
            // position: fixed;
            // bottom: 8.375rem;
            margin-top: 4rem;
        }
    }

</style>


