export default class Recorder {
    constructor(stream, config) {
        //
        window.URL = window.URL || window.webkitURL;
        navigator.getUserMedia = navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.msGetUserMedia;
 
        config = config || {};
        config.sampleBits = config.sampleBits || 16;   // 8, 16
        config.sampleRate = config.sampleRate || 8000; //(1/6 44100)
 
        this.context = new (window.webkitAudioContext || window.AudioContext)();
        this.audioInput = this.context.createMediaStreamSource(stream);
        this.createScript = this.context.createScriptProcessor || this.context.createJavaScriptNode;
        this.recorder = this.createScript.apply(this.context, [4096, 1, 1]);
 
        this.audioData = {
            size: 0,          //
            buffer: [],    //
            inputSampleRate: this.context.sampleRate,   //
            inputSampleBits: 16,     // 8, 16
            outputSampleRate: config.sampleRate,   //
            oututSampleBits: config.sampleBits,       // 8, 16
            input: function (data) {
                this.buffer.push(new Float32Array(data));
                this.size += data.length;
            },
            compress: function () { //
                //
                let data = new Float32Array(this.size);
                let offset = 0;
                for (let i = 0; i < this.buffer.length; i++) {
                    data.set(this.buffer[i], offset);
                    offset += this.buffer[i].length;
                }
                //
                let compression = parseInt(this.inputSampleRate / this.outputSampleRate);
                let length = data.length / compression;
                let result = new Float32Array(length);
                let index = 0, j = 0;
                while (index < length) {
                    result[index] = data[j];
                    j += compression;
                    index++;
                }
                return result;
            },
            encodeWAV: function () {
                let sampleRate = Math.min(this.inputSampleRate, this.outputSampleRate);
                let sampleBits = Math.min(this.inputSampleBits, this.oututSampleBits);
                let bytes = this.compress();
                let dataLength = bytes.length * (sampleBits / 8);
                let buffer = new ArrayBuffer(44 + dataLength);
                let data = new DataView(buffer);
 
                let channelCount = 1;//
                let offset = 0;
 
                let writeString = function (str) {
                    for (let i = 0; i < str.length; i++) {
                        data.setUint8(offset + i, str.charCodeAt(i));
                    }
                };
 
                // 
                writeString('RIFF');
                offset += 4;
                // ,-8
                data.setUint32(offset, 36 + dataLength, true);
                offset += 4;
                // WAV
                writeString('WAVE');
                offset += 4;
                // 
                writeString('fmt ');
                offset += 4;
                // , 0x10 = 16
                data.setUint32(offset, 16, true);
                offset += 4;
                //  (PCM)
                data.setUint16(offset, 1, true);
                offset += 2;
                // 
                data.setUint16(offset, channelCount, true);
                offset += 2;
                // ,,
                data.setUint32(offset, sampleRate, true);
                offset += 4;
                //  () /8
                data.setUint32(offset, channelCount * sampleRate * (sampleBits / 8), true);
                offset += 4;
                //   /8
                data.setUint16(offset, channelCount * (sampleBits / 8), true);
                offset += 2;
                // 
                data.setUint16(offset, sampleBits, true);
                offset += 2;
                // 
                writeString('data');
                offset += 4;
                // ,-44
                data.setUint32(offset, dataLength, true);
                offset += 4;
                // 
                if (sampleBits === 8) {
                    for (let i = 0; i < bytes.length; i++, offset++) {
                        let s = Math.max(-1, Math.min(1, bytes[i]));
                        let val = s < 0 ? s * 0x8000 : s * 0x7FFF;
                        val = parseInt(255 / (65535 / (val + 32768)));
                        data.setInt8(offset, val, true);
                    }
                } else {
                    for (let i = 0; i < bytes.length; i++, offset += 2) {
                        let s = Math.max(-1, Math.min(1, bytes[i]));
                        data.setInt16(offset, s < 0 ? s * 0x8000 : s * 0x7FFF, true);
                    }
                }
                return new Blob([data], {type: 'audio/wav'});
            }
        };
    }
 
    //
    start() {
        this.audioInput.connect(this.recorder);
        this.recorder.connect(this.context.destination);
 
        //
        let self = this;
        this.recorder.onaudioprocess = function (e) {
            // console.log('', e);
            self.audioData.input(e.inputBuffer.getChannelData(0));
        };
    };
 
    //
    stop() {
        this.recorder.disconnect();
    };
 
    //
    getBlob() {
        this.stop();
        return this.audioData.encodeWAV();
    };
 
    //
    play(audio) {
        audio.src = window.URL.createObjectURL(this.getBlob());
    };
 
    //
    clear() {
        this.audioData.buffer = [];
        this.audioData.size = 0;
    };
 
    static throwError(message) {
        console.log("Error:" + message);
        throw new function () {
            this.toString = function () {
                return message;
            }
        };
    };
 
    static canRecording() {
        return (navigator.getUserMedia != null);
    }
 
    static get(callback, config) {
        if (callback) {
            if (Recorder.canRecording()) {
                navigator.getUserMedia(
                    {audio: true}, //
                    function (stream) {
                        let rec = new Recorder(stream, config);
                        callback(rec);
                    },
                    function (error) {
                        switch (error.code || error.name) {
                            case 'PERMISSION_DENIED':
                            case 'PermissionDeniedError':
                                Recorder.throwError('');
                                break;
                            case 'NOT_SUPPORTED_ERROR':
                            case 'NotSupportedError':
                                Recorder.throwError('');
                                break;
                            case 'MANDATORY_UNSATISFIED_ERROR':
                            case 'MandatoryUnsatisfiedError':
                                Recorder.throwError('');
                                break;
                            default:
                                Recorder.throwError(':' + (error.code || error.name));
                                break;
                        }
                    });
            } else {
                Recorder.throwError('');
                return;
            }
        }
    };
}


