<template>
    <div id="indexs">
        <head-back>
            <span class="head-title"></span>
        </head-back>
        <div class="indexs">
            <div class="video-show" v-if="detailInfo.is_img == 1 && detailInfo.works_url">
                <video controls :src="detailInfo.works_url"></video>
            </div>
            <div class="icon" v-if="detailInfo.is_img == 0 && detailInfo.works_url">
                <img :src="detailInfo.works_url" alt="">
            </div>
            <div class="index-bgc">
                <!--  works_url -->
                <div class="icon" v-if="!detailInfo.works_url">
                    <img src="../../../../../../assets/icon/upvideo.png" alt="">
                </div>
                <!--  -->
                <!-- <p class="status" v-show="eventInfo[0].status == 1"></p>
                <p class="status" v-show="eventInfo[0].status == 2"></p>
                <p class="status" v-show="eventInfo[0].status == 3"></p>
                <p class="status" v-show="eventInfo[0].status == 4"></p> -->
                <p class="status" v-if="eventInfo[0].status==1"></p>
                <p class="status" v-else-if="eventInfo[0].status==2"></p>
                <p class="status" v-else-if="eventInfo[0].status==3"></p>
                <p class="status" v-else-if="eventInfo[0].status==4"></p>
                <p class="status" v-else-if="eventInfo[0].status==5"></p>
                <p class="status" v-else-if="eventInfo[0].status==6"></p>
                <p class="status" v-else-if="eventInfo[0].status==7"></p>
                <p class="status" v-else-if="eventInfo[0].status==8"></p>

                <!--  -->
                <div class="sign">
                    <p></p>
                    <p>{{eventInfo[0].competition_name}}</p>
                    <p>{{eventInfo[0].type_name}}</p>
                </div>
                <!--  -->
                <div class="userinfo">
                    <div class="flex flex--justify-content--space-between flex--align-items--center">
                        <span>{{eventInfo[0].user_name}}</span>
                        <span>{{eventInfo[0].sex == 1 ? '' : ''}}</span>
                    </div>
                    <div class="flex flex--justify-content--space-between flex--align-items--center">
                        <span>{{eventInfo[0].address}}</span>
                        <span>{{eventInfo[0].venue}}</span>
                    </div>
                    <div class="flex flex--justify-content--space-between flex--align-items--center">
                        <span>{{eventInfo[0].phone}}</span>
                    </div>
                </div>

                <!--   -->
                <div class="signnum flex flex--justify-content--space-between">
                    <span>{{eventInfo[0].identity_number}}</span>
                    <span>{{eventInfo[0].ctime | timeFilter}}</span>
                </div>

                <!--  -->
                <div class="options" v-show="eventInfo[0].status == 5">
                    <p>{{eventInfo[0].apply_end_time | timeFilter}}</p>
                    <p>{{eventInfo[0].apply_end_time | timeFilter}}</p>
                    <div class="button" @click="$router.push(`/my/event/upload`)"></div>
                </div>

                <div class="options" v-show="type == 1 && status == 1">
                    <div class="button" @click="openPayModle"></div>
                </div>
            </div>
        </div>

        <chose-pay-type 
            ref="choseType" 
            :orderdetail="detailInfo" 
            @closeModle="closeModle"
            :needPayType="1"
            :position="'bottom'"></chose-pay-type>

    </div>
</template>

<script>
import HeadBack from '@/components/headback/headback'
import api from '@/api';
import chosePayType from '@/components/chosePayType';
export default {
    components:{
        HeadBack,
        chosePayType
    },
    created() {
        // console.log('===1111=====', this.detailInfo);
        console.log('===2222=====', this.eventInfo);
    },
    data() {
        return {
            eventInfo: JSON.parse(window.sessionStorage.getItem('eventDetails')),
            type: this.$route.query.type,
            status: this.$route.query.status,
            detailInfo: JSON.parse(this.$route.query.detailInfo),
            orderInfo: null,
        }
    },
    methods: {
        /**
         * 
         */
        openPayModle(){
            this.$refs.choseType.open();
        },
        closeModle(){
            // this.showPayType = false;
            console.log('showPayType');
        },

        /**
         * 
         */
        async goPay(){
            const {data:ret} = await this.$http.post(this.setApi + api.paymoney,this.querystring.stringify({
                pay_type: 3,
                access_token: window.localStorage.getItem('token'),
                amount: this.eventInfo[0].money,
                competition_id: this.eventInfo[0].competition_id,
                competition_type_id: this.eventInfo[0].competition_id
            }))
            // console.log(ret);
            if(!ret.error_code){
                this.$router.replace('/event/pay/sign-success?amount='+this.eventInfo[0].money)
            }else{
                this.$toast(ret.error_msg);
            }
        }
    },
}
</script>

<style lang="less" scoped>
@import url('./index.less');
.video-show {
    height: 200px;
}
.video-show video {
    display: block;
    width: 100%;
    height: 100%;
}
</style>



