<template>
    <div id="exam-detail">
        <head-back>
            <span class="head-title">
                
            </span>
        </head-back>
        <div class="exam-detail">
            <div class="exam-bg">
                <!--  -->
                <div class="exam-logo flex flex--align-items--center flex--justify-content--center">
                    <img src="../../../../assets/icon/signgo.png" alt="">
                </div>
                <!--  status -->
                <div class="exam-status">
                    <span v-if="$route.params.id==1"></span>
                    <span v-else-if="$route.params.id==2"></span>
                    <span v-if="$route.params.id==3"></span>
                    <span v-if="$route.params.id==4"></span>
                </div>
                <!--  -->
                <div class="exam-examinfo">
                    <p></p>
                    <p>{{examDetailList[0].e_name}}</p>
                </div>
                <!--  -->
                <div class="exam-student flex flex--justify-content--space-between">
                    <div>
                        <p>{{examDetailList[0].ea_name}}</p>
                        <p>{{examDetailList[0].phone}}</p>
                    </div>
                    <div>
                        <p>{{examDetailList[0].sex}}</p>
                        <p>{{examDetailList[0].address}}</p>
                    </div>
                </div>
                <!--   -->
                <div class="exam-number flex flex--justify-content--space-between">
                    <span>{{examDetailList[0].identity_number}}</span>
                    <span>{{examDetailList[0].ctime}}</span>
                </div>
                <!--  -->
                <div class="exam-prompt" v-show="$route.params.id == 2">
                    <p>{{examDetailList[0].exa_start_time}}-{{examDetailList[0].exa_end_time}}</p>
                    <p>{{examDetailList[0].exa_end_time}}</p>
                    <van-button @click="show = !show" type="primary"></van-button>
                </div>
                <!--  -->
                <div class="exam-wait" v-show="$route.params.id == 3">
                    <van-button type="primary"></van-button>
                </div>
                <!--  -->
                <div class="exam-public" v-show="$route.params.id == 4">
                    <p>{{examDetailList[0].score}}</p>
                    <van-button type="primary"></van-button>
                    <van-button plain type="primary"></van-button>
                </div>

                <!--  -->
                <van-popup v-model="show">
                    <div class="goExam">
                        <p>50</p>
                        <p><span>50</span></p>
                        <van-button @click="$router.push(`/my/startexam/${$route.params.type_id}/${$route.params.pid}`)" plain hairline type="primary"></van-button>
                    </div>
                </van-popup>
            </div>
        </div>
    </div>
</template>

<script>
import HeadBack from '../../../../components/headback/headback'
import api from '../../../../api/index.js'
export default {
    components:{
        HeadBack
    },
    created() {
        console.log(this.$route)
        this.getExamDetails()
    },
    data() {
        return {
            show:false,
            examDetailList:[
                {e_name:''}
            ]
        }
    },
    methods: {
        // 
        async getExamDetails(){
            const {data:ret} = await this.$http.post(this.setApi + api.examDetails,this.querystring.stringify({examination_apply_id:this.$route.params.pid,access_token:window.localStorage.getItem('token')}))
            console.log(ret.response_data.lists)
            this.examDetailList = ret.response_data.lists
        }
    },
}
</script>

<style lang="less" scoped>
@import url('./examdetail.less');
</style>




