<template>
    <div id="live">
        <div class="live">
            <!-- <video autoplay src="http://live.iyajiao.club/live/tianyu.m3u8"></video> -->
            <video-player class="video-player vjs-custom-skin"
            ref="videoPlayer"
            :playsinline="true"
            :options="playerOptions"
        ></video-player>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            playerOptions: {
                //playbackRates: [0.7, 1.0, 1.5, 2.0], //
                autoplay: true, //true,
                muted: false, // 
                loop: false, // 
                preload: 'auto', // <video>auto,
                language: 'zh-CN',
                aspectRatio: '16:9', //  - "16:9""4:3"
                fluid: true, // trueVideo.js player
                sources: [{
                    type: "application/x-mpegURL",
                    // type:'rtmp/mp4',
                    src: "http://live.iyajiao.club/live/tianyu.m3u8" //m3u8
                    // src:'rtmp://46463.livepush.myqcloud.com/live/tianyu?txSecret=c4fd1c165df5bf307790d7005cd1ffc8&txTime=5CBDE4FF'
                }],
                poster: "poster.jpg", //
                width: document.documentElement.clientWidth,
                notSupportedMessage: '', //Video.js
                //  controlBar: {
                //   timeDivider: true,
                //   durationDisplay: true,
                //   remainingTimeDisplay: false,
                //   fullscreenToggle: true //
                //  }
            }
        }
    },
}
</script>

<style lang="less" scoped>
@import url('./live.less');
</style>



