<template>
    <div id="upvideo">
        <head-back>
            <span class="head-title"></span>
        </head-back>
        <div class="upvideo">
            <div class="uptitle"></div>
            <div class="options">
                <label for="uploads">
                    <div class="up">
                        <p>+</p>
                        <span>{{textToast}}</span>
                    </div>
                </label>
                <input type="file" @change="onRead" v-show="false" id="uploads">
                <p class="again" @click="clear"></p>
                <div v-if="showOperated" class="video flex flex--justify-content--space-between flex--align-items--center">
                    <div class="flex flex--align-items--center">
                        <img class="video-file" src="../../../../../../assets/video-file.png" alt="">
                        <p>{{files.name}}</p>
                    </div>
                    <div @click="clear" class="close flex flex--align-items--center">
                        <img class="video-close" src="../../../../../../assets/icon/close.png" alt="">
                    </div>
                </div>
                <div @click="upVideo" class="submit">
                    
                </div>
                <!-- <div class="button" @click="$router.push(`/my/event/email`)">
                    
                </div> -->
            </div>
            <!--  --> 
            <div v-show="lineShow" class="jindutiao">
                <p>{{jindu}}</p>
                <div ref="jinduline"></div>
            </div>
            <div class="shuoming flex">
                <img src="../../../../../../assets/icon/tanhao.png" alt="">
                <p>
                    <br/>
                    1442801340@qq.com<br/>
                    010-6938512269389836<br/>
                </p>
            </div>
        </div>
        <input type="hidden" name="video" id="video">
                        <iframe id="iFrame1" name="iFrame1" width="100%" height="250px"  frameborder="0" src="http://aidingdong_admin.taijichn.com/Ossupload/index.html"></iframe>

        <loading v-if="showLoading" />
    </div>
</template>

<script>
import { Toast } from 'vant';
import HeadBack from '@/components/headback/headback';
import loading from '@/components/loading';
import api from '@/api';

export default {
    components:{
        HeadBack,
        loading
    },
    data() {
        return {
            files:null,
            showOperated: false,
            lineShow:false,
            jindu:'0%',
            textToast:'/',
            eventInfo: JSON.parse(window.sessionStorage.getItem('eventDetails')),
            showLoading: false
        }
    },
    created(){
        console.log('eventInfo===', this.eventInfo);
    },
    methods: {
        /**
         * 
         */
        clear(){
            this.showOperated = false;
            this.files = null;
        },

        /**
         * 
         */
        onRead(e){
            this.files = document.querySelector('#uploads').files[0];
            this.showOperated = true;
            // console.log('-----------------', e);
            // console.log('=================', this.files);
            this.$toast('');
            // this.upVideo();
        },

        /**
         * 
         */
        async upVideo(){
            let _this = this;
            let times = 0;
            let success = 0;

            if(!this.files){
                this.$toast('');
                return;
            }
            // this.showLoading = true;
            
            let timeClire = Toast.loading({
                duration: 0,       //  toast
                forbidClick: true, // 
                loadingType: 'spinner',
                mask: true,
                message: times + '%'
            });

            _this.showLoading = false;

            let timer = setInterval(() => {
                times++;
                timeClire.message = times + '%';
                if(times == 99){
                    clearInterval(timer);
                    // if(success == 0){
                    //     timeClire.message = ''
                    // }else{
                    //     timeClire.message = ''
                    // }

                    // setTimeout(() => {
                    //     Toast.clear();
                    // }, 500);
                }

            }, 50);

            // this.lineShow = true;
            var formData = new FormData();
            formData.append('file', this.files);
            formData.append('id', this.eventInfo[0].identity_number);
            formData.append('type', 1);       // 
            const {data:ret} = await this.$http.post(this.setApi + api.zuopinUp,formData,{
                onUploadProgress(res){
                    if(res.total > 262144000) return this.$toast('');
                    // let jindu = Math.floor((res.loaded / res.total) * 100 );
                    // _this.jindu = `${jindu}%`;
                    // _this.$refs.jinduline.style.width = `${jindu}%`;
                    // if(jindu == 100) return _this.$toast('');
                    _this.lineShow = false;
                }
            
            })

            Toast.clear();

            if(!ret.error_code){
                success = 1;
                _this.textToast = '';
                timeClire.message = ''
                this.showLoading = false;
                _this.$router.go(-2);
            }else{
                success = 0;
                 _this.textToast = '';
                timeClire.message = ''
                _this.$router.go(-2);
            }
        }

    },
}
</script>

<style lang="less" scoped>
@import url('./index.less');
.video .video-close,
.video .video-file {
    display: block;
    width: 20px;
}
.video .video-file {
    margin-right: 10px;
}
.video {
    font-size: 12px;
    padding: 20px 0;
}
</style>




